<template>
  <div class="store-test">
    <h1>{{ msg }}</h1>

    <div style="display: flex;width: 100%" class="home-content" v-for="(items, index) in quickMenuList.categoryMenus"
         :key="index">
      <div class="log-name" style="width:100%;">
        <!-- <div class="log-style">{{items.cateoryName}}</div> -->
        <!-- <div style="width: 100%; display: flex;"> -->
        <div v-for="(item, index) in items.menus" :key="index" style="width:100%;display:flex;padding:5px 10px" @click="goTarget(item)">
          <!-- <badge v-show="item.menuName==='公告'"></badge> -->
          <!-- <img v-if="item.menuIcon" slot="icon" :src="item.menuIcon"> -->
          <br>
          <div class="description" v-if="item.menuName">{{item.menuName}}</div>
        </div>
        <!-- </div> -->
      </div>
    </div>
  </div>
</template>

<script>

import mainTab from '../data/mainTab.json'

export default {
  name: 'Tab',
  computed: {},
  data () {
    return {
      msg: 'Welcome to Your Tab Page！',
      quickMenuList: {}
    }
  },
  mounted () {
    this.quickMenuList = mainTab.menuCateories
    // console.log(JSON.stringify(this.quickMenuList.categoryMenus[0]))
  },
  methods: {
    goTarget (item) {
      this.$router.push('/' + item.selectUrl)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
  .home-content {
    width: 100%
  }

  img {
    width: 50px;
    height: 50px;
  }
</style>



